.receiver-text-div {
  display: flex;
  justify-content: flex-end;
  margin: 1px 0;
}
.receiver-text-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.receiver-text {
  background: linear-gradient(
      180deg,
      rgba(139, 47, 184, 1) 0%,
      rgba(103, 88, 205, 1) 51%,
      rgba(89, 116, 219, 1) 92%
    )
    no-repeat center;
  background-attachment: fixed;
  color: #fff;
  border-radius: 1rem;
  height: fit-content;
  width: fit-content;
  padding: 0.5rem 1rem;
  margin: 0.12rem 0.5rem;
}

.receiver-img {
  display: block;
  float: left;
}

