.sender-text-div {
  display: flex;
  margin: 1px;
}

.sender-text-div > img {
  height: 2rem;
  align-self: flex-end;
}

.sender-text-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.sender-text {
  background: #262626;
  border-radius: 1rem;
  color: #fff;
  height: fit-content;
  width: fit-content;
  padding: 0.5rem 1rem;
  margin: 0.12rem 0.5rem;
}

.sender-img {
  float: right;
}
